<template>
  <transition name="fade" tag="div" class="wrapper" mode="out-in">
    <div class="wrapper" v-if="isLoaded" id="app">
      <LandingPage :user="user" />
      <Description :user="user" :content="findSlug('description')" :links="findSlug('links')" />
      <Experience :content="findSlug('experiences')" />
      <Skills :content="findSlug('skills')" />
      <Projects :content="findSlug('projects')" />
      <Contact />
      <Footer :links="findSlug('links')" :layout="layout" />
      <BackToTop />
    </div>
  </transition>
</template>

<script>
import LandingPage from "./components/LandingPage.vue";
import Description from "./components/Description.vue";
import Experience from "./components/Experience.vue";
import Skills from "./components/Skills.vue";
import Projects from "./components/Projects.vue";
import Footer from "./components/Footer.vue";
import BackToTop from "./components/BackToTop.vue";
import Contact from "./components/Contact.vue";


import { bucket } from "./cosmic.js";

export default {
  name: "App",
  components: {
    LandingPage,
    Description,
    Experience,
    Skills,
    Projects,
    Footer,
    BackToTop,
    Contact,
  },
  data: () => ({
    isLoaded: false,
    user: {},
    posts: [],
    layout: {
      footer: {
        copyright: "Viet Anh Dang",
      },
      head: {
        icon: "",
        description: "Viet Anh Dang",
        keywords: "Viet Anh Dang",
        author: "Viet Anh Dang",
        title: "Viet Anh Dang",
      },
    }
  }),
  methods: {
    fetchPosts() {
      return bucket.getObjects({
        type: "portfolio-contents",
        props: "slug,title,metadata",
      });
    },
    fetchUser() {
      return bucket.getObjects({
        type: "portfolio-contents",
        q: "user-data",
        props: "slug,title,metadata",
      });
    },
    fetchLayout() {
      return bucket.getObjects({
        type: "portfolio-contents",
        q: "layout",
        props: "slug,title,metadata",
      });
    },
    fetchObjectTypes() {
      return bucket.getObjectTypes();
    },
    findSlug(slug) {
      return this.posts.find((item) => {
        return item.slug === slug;
      });
    },
    extractFirstObject(objects) {
      if (objects.objects == null)
        return void 0;
      else
        return objects.objects[0];
    },
    /**
     * @description: Hàm này dùng để hiển thị thông tin head của trang web
     * @param: {any} 
     * Author: AnhDV 29/01/2023
     */
    showTabInfo() {
      let me = this;
      document.title = me.layout.head.title;
      document.querySelector('meta[name="description"]').setAttribute("content", me.layout.head.description);
      document.querySelector('meta[name="keywords"]').setAttribute("content", me.layout.head.keywords);
      document.querySelector('meta[name="author"]').setAttribute("content", me.layout.head.author);
      document.querySelector('link[rel="icon"]').setAttribute("href", me.layout.head.icon);

    },
  },
  created() {
    document.body.classList.add("loading");
    Promise.all([this.fetchPosts(), this.fetchUser(), this.fetchLayout()]).then(([posts, user_data, layout_config]) => {
      user_data = this.extractFirstObject(user_data);
      layout_config = this.extractFirstObject(layout_config);
      this.posts = posts.objects;
      this.user = {
        name: user_data.metadata.name,
        status: user_data.metadata.status,
        email: user_data.metadata.email,
        phone: user_data.metadata.phone,
        city: user_data.metadata.city,
        live: user_data.metadata.live,
        lang: user_data.metadata.lang,
        photo: user_data.metadata.photo,
      }
      this.layout = {
        footer: layout_config.metadata.layout.footer,
        head: layout_config.metadata.layout.head,
      }
      this.isLoaded = true;
      this.$nextTick(() => {
        document.body.classList.remove("loading");
        this.showTabInfo();
      });
    });
  },
 
};
</script>

<style scoped lang="scss">
@import "@/styles/constants.scss";

#app {
  font-family: Montserrat-Regular, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.wrapper {
  height: 100%;
}
</style>
