<template>
    <div class="contact" id="contact">
        <div class="contact-container">
            <Title class="title" title="Contact" description="If you have any questions or want to work with me, please contact me
                using the form below." />
            <validation-observer ref="observer" v-slot="{ handleSubmit, dirty }">
                <b-form @submit.stop.prevent="handleSubmit(onSubmit)" class="contact-form" ref="form">
                    <validation-provider name="Name" :rules="{ required: true, min: 3 }" v-slot="validationContext">
                        <b-form-group id="example-input-group-1" label="Name" label-for="name">
                            <b-form-input id="name" name="name" v-model="contact.name"
                                :state="getValidationState(validationContext)" placeholder="Enter your name"
                                aria-describedby="input-1-live-feedback"></b-form-input>

                            <b-form-invalid-feedback id="input-1-live-feedback">{{
                                validationContext.errors[0]
                            }}</b-form-invalid-feedback>
                        </b-form-group>
                    </validation-provider>

                    <validation-provider name="Email" :rules="{ required: true, email: true }"
                        v-slot="validationContext">
                        <b-form-group id="example-input-group-2" label="Email" label-for="email">
                            <b-form-input id="email" name="email" v-model="contact.email"
                                :state="getValidationState(validationContext)" placeholder="Enter your email address"
                                aria-describedby="input-2-live-feedback"></b-form-input>

                            <b-form-invalid-feedback id="input-2-live-feedback">{{
                                validationContext.errors[0]
                            }}</b-form-invalid-feedback>
                        </b-form-group>
                    </validation-provider>

                    <validation-provider name="Message" :rules="{ required: true, min: 10 }" v-slot="validationContext">
                        <b-form-group id="example-input-group-3" label="Message" label-for="message">
                            <b-form-textarea id="message" name="message" v-model="contact.message" rows="8"
                                placeholder="Enter your message" :state="getValidationState(validationContext)"
                                aria-describedby="input-3-live-feedback"></b-form-textarea>

                            <b-form-invalid-feedback id="input-3-live-feedback">{{
                                validationContext.errors[0]
                            }}</b-form-invalid-feedback>
                        </b-form-group>
                    </validation-provider>

                    <div class="d-flex justify-content-between">
                        <b-button type="reset" variant="danger" @click="resetForm()" v-if="dirty">Reset</b-button>
                        <div class="mr-3" v-if="dirty"></div>
                        <b-overlay :show="busy" rounded opacity="0.6" spinner-small spinner-variant="primary"
                            class="d-inline-block w-100">
                            <b-button type="submit" variant="primary" :disabled="busy">Submit</b-button>
                        </b-overlay>
                    </div>
                </b-form>
            </validation-observer>
        </div>
    </div>
</template>

<script>
import Title from "./Title.vue";
import emailjs from '@emailjs/browser';
export default {
    name: 'Contact',
    components: {
        Title
    },
    data() {
        return {
            contact: {
                name: null,
                email: null,
                message: null
            },
            errors: [],
            busy: false
        }
    },

    methods: {
        getValidationState({ dirty, validated, valid = null }) {
            return dirty || validated ? valid : null;
        },
        resetForm() {
            this.$nextTick(() => {
                this.$refs.observer.reset();
                this.$el.querySelector('#name').focus();
            });
            this.contact = {
                name: null,
                email: null,
                message: null
            };
        },
        makeToast(variant = null, title = null, body = null) {
            this.$bvToast.toast(body, {
                title: title,
                variant: variant,
                solid: true
            })
        },
        onSubmit() {
            try {
                this.busy = true;
                emailjs.sendForm('service_kypvvhp', 'template_exdcnw9', this.$refs.form, 'UVnAQQl5nuvaceAa7')
                    .then((result) => {
                        console.log(result.text);
                        this.makeToast('success', 'Message sent successfully', 'I will get back to you as soon as possible');
                        this.resetForm();
                        this.busy = false;
                    }, (error) => {
                        console.log(error.text);
                    });
            } catch (error) {
                this.makeToast('danger', 'Message not sent', 'Please try again later');
                console.log(error);
            }
        },
       
    },
}
</script>

<style scoped lang="scss">
@import '@/styles/constants.scss';

$bg-contact: lighten(map-get($colors, primary), 100%);

/deep/ .toast-header {
    background-color: map-get($colors, primary);
    color: map-get($colors, white);
    font-weight: bold;
    font-size: 1.2rem;
}

/deep/ .toast-body {
    background-color: map-get($colors, white);
    color: map-get($colors, primary);
    font-size: 1.2rem;

}


.contact {
    padding: 50px 0;
    background-color: $bg-contact;
    color: map-get($colors, primary);
    text-align: center;
    
}


.contact-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid map-get($colors, primary);
    border-radius: 4px;
}

.contact-form {
    width: 50%;
    margin: 0 auto;
}

.form-group {
    margin-bottom: 20px;
}

/deep/ label {
    display: block;
    margin-bottom: 5px;
    color: map-get($colors, primary);
    font-weight: bold;
}

.form-control {
    width: 100%;
    height: 40px;
    border: 1px solid map-get($colors, primary);
    border-radius: 0;
    font-size: 1.2rem;
    padding: 0 10px;
    border-radius: 4px;
}

.btn {
    width: 100%;
    height: 40px;
    border: 1px solid map-get($colors, primary);
    border-radius: 0;
    background-color: map-get($colors, primary);
    color: map-get($colors, light);
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
}

/deep/ textarea {
    resize: none;
    padding: 10px !important;
    height: auto !important;
}
</style>