<template>
    <div class="back-to-top" @click="scrollToTop" v-b-tooltip.hover="{ customClass: 'tooltip' }"
        title="Back to top">
        <font-awesome-icon icon="arrow-up" class="icon" />
    </div>
</template>

<script>
export default {
    name: 'BackToTop',
    methods: {
        scrollToTop() {
            window.scrollTo({
                top: 0,
                behavior: 'smooth'
            })
        }
    },
    mounted() {
        window.addEventListener('scroll', () => {
            const backToTop = document.querySelector('.back-to-top')
            if (window.pageYOffset > 100) {
                backToTop.classList.add('show')
            } else {
                backToTop.classList.remove('show')
            }
        })
    }
}
</script>

<style scoped lang="scss">
.back-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    opacity: 0;
    visibility: hidden;
    z-index: 999;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    background-color: #fff;

    &.show {
        opacity: 1;
        visibility: visible;
    }
}

.icon {
    font-size: 2rem;
    vertical-align: middle;
    width: 20px;
    height: 20px;
}

.tooltip {
    font-size: 1.2rem;
}
</style>